/* checkbox */
.styled-checkbox {
    position: absolute;
    opacity: 0;
    width: auto !important;
    height: auto !important;
    margin: auto !important;
}

.styled-checkbox~label {
    position: relative;
    cursor: pointer;
    padding: 0px;
    font-size: 12px;
    text-transform: uppercase;
    opacity: 1;
    transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -moz-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -ms-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.styled-checkbox~label:hover {
    opacity: 0.7;
}

/*// Box.*/
.styled-checkbox~label:before {
    content: '';
    margin-right: 12px;
    display: inline-block;
    vertical-align: middle;
    /* border: 1px solid #231F20; */
    width: 18px;
    height: 18px;
    background: #EEEEEE;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -moz-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -ms-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

/*// Box hover*/
.styled-checkbox:hover~label:before {
    /*background: transparent;*/
}

/*// Box focus*/
.styled-checkbox:focus~label:before {
    /* box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12); */
}

/*// Box checked*/
.styled-checkbox:checked+label:before {
    /*background: transparent;*/
}

/*// Disabled state label.*/
.styled-checkbox:disabled~label {
    color: #b8b8b8;
    cursor: auto;
}

/*// Disabled box.*/
.styled-checkbox:disabled~label:before {
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    background: #ddd;
}

/*// Checkmark. Could be replaced with an image*/
.styled-checkbox:checked~label:after {
    content: '';
    position: absolute;
    left: 3px;
    top: -2px;
    width: 20px;
    height: 14px;
    background: url(../img/images/ar-rad.svg) no-repeat top center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -moz-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    -ms-transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.styled-checkbox~label>a {

    opacity: 1;
    transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
    -moz-transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
    -ms-transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
}

.styled-checkbox~label>a:hover {
    opacity: 0.7;
}

