    display: -webkit-box;
    display: flex;
    -ms-flex-direction: row;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: start;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -ms-flex-line-pack: start;
    align-content: flex-start;
    -webkit-align-items: flex-start;
    align-items: flex-start;
    -ms-flex-align: start;
}

.podcast-item {
    display: block;
    /* box-sizing: content-box; */
    width: calc(100% / 3);
    border-bottom: 2px solid #F3F3F3;
    margin-bottom: 42px;
}

.podcast-item-cont {
    padding-bottom: 32px;
    margin-bottom: 32px;
    padding-right: 52px;
    padding-left: 52px;
    border-right: 2px solid #F3F3F3;
    /* box-sizing: content-box; */
}

.podcast-item-vis {
    position: relative;
    border-radius: 16px;
    overflow: hidden;
    width: 100%;
    height: 330px;
    padding: 38px 44px;
    text-align: center;
    margin-bottom: 28px;
}

.podcast-item__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.podcast-item__bg img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: 2.3s;
    transition: 2.3s;
}

.podcast-item:hover .podcast-item__bg img {
    -webkit-transform: scale(1.6);
    transform: scale(1.6);
}

.podcast-item__logo {
    position: relative;
    width: 192px;
    height: 26px;
    background: url(../img/podcast-item-logo.svg) no-repeat center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    margin: 0 auto;
    margin-bottom: 48px;
    z-index: 2;
}

.podcast-item__icon {
    position: relative;
    width: 146px;
    height: 104px;
    background: url(../img/podcast-item-play.svg) no-repeat center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    margin: 0 auto;
    margin-bottom: 58px;
    z-index: 2;
}

.podcast-item__text {
    position: relative;
}

.podcast-item__text span {
    font-size: 18px;
    text-transform: uppercase;
    color: #FFF;
}

.podcast-item:nth-child(3n+3) {
    padding-right: 0;
    margin-right: 0;
}

.podcast-item:nth-child(3n+3) .podcast-item-cont {
    border-right: 0 !important;
    padding-right: 0 !important;
}

.podcast-item:nth-last-child(4)~.podcast-item:nth-child(3n+1) .podcast-item-cont,
.podcast-item:nth-last-child(4)~.podcast-item:nth-child(3n+1)~.podcast-item .podcast-item-cont {
    margin-bottom: 0;
    border-bottom: none;
    padding-bottom: 0;
}

.podcast-item:nth-last-child(4)~.podcast-item:nth-child(3n+1),
.podcast-item:nth-last-child(4)~.podcast-item:nth-child(3n+1)~.podcast-item {
    margin-bottom: 0;
    border-bottom: none;
}

.podcast-item:nth-child(3n+1) .podcast-item-cont {
    padding-left: 0;
}

.podcast-item-info {}

.podcast-item__title {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    height: 58px;
    margin-bottom: 38px;
    opacity: 1;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    -moz-transition: 0.3s;
    transition: 0.3s;
}

.podcast-item:hover .podcast-item__title {
    opacity: 0.5;
}

.podcast-item__title span {
    font-size: 26px;
    font-weight: 500;
    font-family: 'Platform';
}

.swiper-slide .podcast-item {
    width: 100% !important;
    border: 0;
    margin: 0;
}

.swiper-slide .podcast-item .podcast-item-cont {
    padding: 0;
    border: 0;
}

.podcast-item:nth-child(3n+1),
.podcast-item:nth-child(3n+3) {
    width: calc(100% / 3 - 20px);
}

.podcast-item:nth-child(3n+2) {
    width: calc(100% / 3 + 40px);
}

.env_text>iframe {
    margin-bottom: 42px;
}

.podcast-item:not([style="display: none;"]):nth-child(3n+1) .podcast-item-cont {}

.podcast-item:not([style="display: none;"]):nth-child(3n+2) .podcast-item-cont {}

.general-bottom-between {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-align-content: center;
    -ms-flex-line-pack: center;
    align-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}


