/* Mailing.css */

.main__inner .mailing {
    background-color: #0257A6 !important;
    position: relative;
    overflow-x: hidden;
}

.mailing-bg {
    position: absolute;
    width: auto;
    height: 74%;
    top: 50%;
    left: 0;
    -webkit-transform: translateX(0) translateY(-50%);
    -ms-transform: translateX(0) translateY(-50%);
    transform: translateX(0) translateY(-50%);
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-duration: 40.0s;
    animation-duration: 40.0s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-name: text-line-bg;
    animation-name: text-line-bg;
}

@-webkit-keyframes text-line-bg {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0) translateY(-50%);
        transform: translateX(0) translateY(-50%);
    }

    99% {
        opacity: 1;
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40%) translateY(-50%);
        transform: translateX(-40%) translateY(-50%);
    }
}

@keyframes text-line-bg {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0) translateY(-50%);
        transform: translateX(0) translateY(-50%);
    }

    99% {
        opacity: 1;
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40%) translateY(-50%);
        transform: translateX(-40%) translateY(-50%);
    }
}

.mailing-bg img {
    height: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}

