    font-size: 20px;
    line-height: 150%;
    color: #FFF;
}

.mailing-form__input input.invalid {
    color: #FF8EAA;
    border-color: #FF8EAA;
}

.mailing-form__btn {
    padding: 15px 35px;
    position: absolute;
    top: 7px;
    right: 0;
    cursor: pointer;
}

.mailing-form__btn::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 99px;
    background-color: #fff;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.mailing-form__btn:hover::before {
    -webkit-transform: scale(1.1);
