    line-height: 14px;
    color: #021333;
}

.mailing-checkbox {
    position: absolute;
    opacity: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.mailing-checkbox__label {
    display: block;
    padding-left: 30px;
    position: relative;
    font-size: 14px;
    line-height: 130%;
    color: #FFF;
}

.mailing-checkbox__label a {
    text-decoration: underline;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.mailing-checkbox__label a:hover {
    text-decoration: none;
    opacity: .7;
}

.mailing-checkbox__custom {
    width: 18px;
    height: 18px;
    top: 0;
    left: 0;
    position: absolute;
    background: rgba(255, 255, 255, 0.12);
    border-radius: 3px;
    border: 1px solid transparent;
}

.mailing-checkbox__custom::before {
    opacity: 0;
    content: "";
    width: 16px;
    height: 12px;
    position: absolute;
    top: 0;
    left: 4px;
    background-image: url("../img/mailing-checkbox-checked.svg");
