.fixed-info-block {
    position: fixed;
    top: 50vh;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 0;
    background-color: #FAD7E3;
    z-index: 3;
    padding: 15px 23.5px 25px;
    border-radius: 20px 0 0 20px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.fixed-info-block.hide {
    cursor: pointer;
    -webkit-transform: translateY(-50%) translateX(85%);
    -ms-transform: translateY(-50%) translateX(85%);
    transform: translateY(-50%) translateX(85%);
}

.fixed-info-block:hover {
    padding-left: 40px;
    padding-right: 40px;
}

.fixed-info-block__close {
    cursor: pointer;
    position: relative;
    width: 15px;
    height: 15px;
    margin-left: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-bottom: 15px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.fixed-info-block__close::before,
.fixed-info-block__close::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    z-index: 1;
    background-color: #DA5780;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.fixed-info-block__close::after {
