.input_box,
.textarea_box {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
