  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.input_box:hover input,
.textarea_box:hover textarea {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
  -webkit-box-shadow: inset 0 0 0 2px #161310;
  -moz-box-shadow: inset 0 0 0 2px #161310;
  box-shadow: inset 0 0 0 2px #161310;
}

.textarea_box {
  overflow: hidden;
}

.textarea_resize::before,
.textarea_resize::after {
  cursor: row-resize;
}

.textarea_resize::before {
  content: "";
  position: absolute;
  right: -25px;
  bottom: -25px;
  width: 50px;
  height: 50px;
  /* background-color: rgba(249, 246, 239, 1); */
  background-color: #FAF9F8;
  z-index: 1;
  pointer-events: none;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  z-index: 1;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
}

.textarea_resize::after {
  content: "";
  position: absolute;
  right: 4px;
  bottom: 7px;
  width: 10px;
  height: 3px;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  z-index: 2;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  pointer-events: none;
  cursor: col-resize;
}

textarea:hover ~ .textarea_resize::before {
  background-color: #161310;
}

textarea:hover ~ .textarea_resize::after {
  border-top: 1px solid white;
  border-bottom: 1px solid white;
}

.chars {
  position: absolute;
  top: 10px;
  right: 10px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
