  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.2em 0.5em;
  content: attr(data-placeholder);
  visibility: hidden;
}

.select__box {
  position: absolute;
  top: -webkit-calc(100% + 14px);
  top: -moz-calc(100% + 14px);
  top: calc(100% + 14px);
