  border-color: #FF8EAA;
}

.mailing-form__btn {
  padding: 15px 35px;
  position: absolute;
  top: 7px;
  right: 0;
  cursor: pointer;
}

.mailing-form__btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 99px;
  background-color: #fff;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.mailing-form__btn:hover::before {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.mailing-form__btn span {
  z-index: 1;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  color: #021333;
}

.mailing-checkbox {
  position: absolute;
  opacity: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.mailing-checkbox__label {
  display: block;
  padding-left: 30px;
  position: relative;
  font-size: 14px;
  line-height: 130%;
  color: #FFF;
}

.mailing-checkbox__label a {
  text-decoration: underline;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.mailing-checkbox__label a:hover {
  text-decoration: none;
  opacity: 0.7;
}

.mailing-checkbox__custom {
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  position: absolute;
  background: rgba(255, 255, 255, 0.12);
  border-radius: 3px;
  border: 1px solid transparent;
}

.mailing-checkbox__custom::before {
  opacity: 0;
  content: "";
  width: 16px;
  height: 12px;
  position: absolute;
  top: 0;
  left: 4px;
  background-image: url("../img/mailing-checkbox-checked.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.mailing-checkbox:checked ~ .mailing-checkbox__label .mailing-checkbox__custom::before {
  opacity: 1;
}

.mailing-checkbox.invalid ~ .mailing-checkbox__label .mailing-checkbox__custom {
  border-color: #FF8EAA;
}

